interface IconProps {
  width?: string;
  height?: string;
  className?: string;
}

const AddIcon = ({ width = '20', height = '20', className = '' }: IconProps) => {
  return (
      <span className={className}>
          <svg
              width={width}
              height={height}
              viewBox="0 0 20 20"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
          >
              <path
                  d="M5 10H15"
                  stroke="currentColor"
                  strokeWidth="1.5"
                  strokeLinecap="round"
                  strokeLinejoin="round"
              />
              <path
                  d="M10 15V5"
                  stroke="currentColor"
                  strokeWidth="1.5"
                  strokeLinecap="round"
                  strokeLinejoin="round"
              />
          </svg>
      </span>
  );
};

export default AddIcon;